import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { NavBackContext } from "../../context/context";
import "../css/TuitionClasses.scss";
import cardImg1 from "../../assets/images/classes_category_1.jpg"
import cardImg2 from "../../assets/images/classes_category_2.jpg"
import cardImg3 from "../../assets/images/classes_category_3.jpg"
import SchoolIcon from '@mui/icons-material/School';

import "../css/TuitionClasses.scss";

const Card = (props)=>{
    return(
        <div className="card dflex dflex-column">
            <figure>
                <img src={props.img} alt="a random online class " />
            </figure>

            <h3>{props.title}</h3>

            <h4>{props.caption}</h4>
            <p>{props.description}</p>
        </div>
    )
}

const cardInfo = [
    {
        id: 1,
        title: 'Private Classes',
        caption: 'Looking for one-to-one classes?',
        description: 'Register Yourself & then Enroll to a course, by choosing the Course & then the Teacher.',
        img: cardImg1
    },

    {
        id: 2,
        title: 'Offline Classes',
        caption: 'Are you left with any doubts?',
        description: 'Yes, we will record your live classes for you. Just subscribe to the videos.',
        img: cardImg2
    },

    {
        id: 3,
        title: 'Group Classes',
        caption: 'Need the classroom experience?',
        description: 'Enroll to our group classes right now & study with your peers.',
        img: cardImg3
    }
]

const TuitionClasses = () => {
  const navback = useContext(NavBackContext);

  return (
    <div className="tuitionClasses-container">
      <div className="navBack" style={{ background: navback.navBack }}></div>

      <section className="tuition_herosection">
        <h2>Want The Best Tuition Classes?</h2>
        <h3>You have come to the right place</h3>
        <button>
          <Link end to="/contact">Hire Our Best Tutors</Link>
        </button>
      </section>

      <div className="small-box dflex dflex-column gap-2">
        <h3 className="text-center">Best Tutors For Online/Offline Tuitions</h3>
        <ol className="dflex wrap gap-1">
          <li className="dflex gap-1">
            <input type="checkbox" id="check1" checked  />
            <label for="check1">Online School For Every Student</label>
          </li>
          <li className="dflex gap-1">
            <input type="checkbox" id="check2" checked />
            <label for="check2">All Subjects And Board</label>
          </li>
          <li className="dflex gap-1"> 
            <input type="checkbox" id="check3" checked  />
            <label for="check2">Online Classes For Class 5 to 12</label>
          </li>
        </ol>

        <h4 className="text-center">
          District Level Examination Preparation | SEE Preparation | SLC
          Preparation | Any Board Exam Preparation Up To Grade 12
        </h4>
      </div>

      <section className="categories">
        <h2 className="text-center">Categories Of Online Classes</h2>
        <div className="sub-heading grid-center">
            <SchoolIcon sx={{color: 'var(--purple)', fontSize: '3rem'}} />
        </div>
        <div className="cards-container dflex gap-1 wrap" > 
        {
            cardInfo.map(card => {
                return <Card key={card.id} img={card.img} title={card.title} caption={card.caption} description={card.description} />
            })
        }
        
        </div>
      </section>
    </div>
  );
};

export default TuitionClasses;
